.default {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 25px;
    margin: 0 15px 15px 0;
    outline: none;
    border: 1px solid #fff;
    border-radius: 50px;
    height: 46px;
    line-height: 46px;
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;
    color: #444;
    background-color: #fff;
    box-shadow: 0 4px 6px rgb(65 132 144 ), 0 1px 3px rgb(0 0 0 );
    cursor: pointer;
    user-select: none;
    appearance: none;
    touch-action: manipulation;
    vertical-align: top;
    transition: box-shadow 0.2s;
}
.default:disabled{
    background-color: #eee;
    border-color: #eee;
    color: #444;
    cursor: not-allowed;
}
.default:focus{
    border: 1px solid #4c51f9;
    outline: none;
}

.default:hover{
    transition: all 0.2s;
    box-shadow: 0 7px 14px rgb(65 132 144 ), 0 3px 6px rgb(0 0 0 );
}

.red {
    background: red;
}